@media only screen 
and (min-device-width : 375px) 
and (max-device-width : 812px) 
and (-webkit-device-pixel-ratio : 3)
and (orientation : landscape) {
  #start_stop{
    background-image: linear-gradient(to right,teal,crimson);
    max-height: 80%;
    max-width: 45%;
    border-radius: 5px;
    font-size: 75%;
    padding: 2%;
  }
  #reset {
    background-color: purple;
    max-height: 80%;
    max-width: 30%;
    border-radius: 5px;
    font-size: 75%;
    padding: 2%;
  }
  #Header {
    height: 20%;
    display: flex;
    align-items: top;
    justify-content: center;
    font-size: 62.5%;
    padding-bottom: 10%;
  }
}

@media only screen 
and (min-device-width : 375px) 
and (max-device-width : 812px) 
and (-webkit-device-pixel-ratio : 3)
and (orientation : portrait) {
  #Header {
    height: 20%;
    display: flex;
    align-items: top;
    justify-content: center;
    font-size: 62.5%;
    padding-bottom: 10%;
  }
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #282c34;
}

#Main {
  display: flex;
  justify-content: center;
  align-items: top;
}

#Header {
  height: 20%;
  display: flex;
  align-items: top;
  justify-content: center;
  font-size: 125%;
  padding-bottom: 10%;
}

h1, h3, #break-length, #session-length {
  color: seashell;
}

.Break-Sesh {
  display: flex;
  justify-content: space-evenly;
  align-items: top;
  text-align: center;
  min-height: 70%;
  width: 80%;
}

.incDec {
  background-color: yellow;
  font-size: 200%;
  font-weight: bold;
  width: 25%;
}

.flex-third {
  height: 100%;
  width: 30%;
  border-radius: 5px;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#timer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 2.5%;
}

#timer-label {
  height: .75%;
}

#time-left {
  width: 80%;
  text-align: center;
  line-height: 200%;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 5px;
}

#time-left p {
  font-size: 300%;
  max-width: 100%;
}

.timer-buttons {
  display: flex;
  justify-content: space-between;
  align-items: top;
  color: whitesmoke;
  width: 80%;
}

#start_stop {
  background-image: linear-gradient(to right,rgb(43, 197, 43),rgb(196, 196, 34));
  max-height: 80%;
  max-width: 45%;
  border-radius: 5px;
  font-size: 150%;
  padding: 2%;
}
#reset {
  background-color: rgb(240, 4, 51);
  max-height: 80%;
  max-width: 30%;
  border-radius: 5px;
  font-size: 150%;
  padding: 2%;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 12%;
  width: 12%;
  pointer-events: none;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg)
  }
}
